.wp-site-blocks {
  padding-top: 10wh;
}

.hidden {
  opacity: 0;
  transition: opacity 0.5s ease;
  pointer-events: none;
}

.categories {
  .videos {
    height: 40vh;
    &.active {
      height: 100vh;
      position: fixed;
      top: 0;
      z-index: 2;
      width: 100%;
    }
    position: relative;
  }
  .overlay {
    z-index: 1;
    position: absolute;
    height: 100%;
    top: 0px;
    left: 0;
    object-fit: cover;
  }
  .category_selector {
    margin-bottom: 10px;
    a {
      padding-right: 10px;
    }
  }

  h2 {
    margin: 5px 0;
    font-family: var(--wp--preset--font-family--asap);
    font-size: 22px !important;

    @media (min-width: 720px) {
      font-size: 26px !important;
      margin: 16px 0;
    }
  }

  a {
    color: var(--wp--preset--color--black) !important;
  }

  table {
    width: 100%;
    td {
      width: auto;
      @media (min-width: 720px) {
        width: 50%;
      }
    }
  }

  // td {
  // @media (min-width: 720px) {
  // 	width: 50%;
  // }
  // }

  img {
    margin-left: 8px;

    @media (min-width: 720px) {
      margin-left: 20px;
    }

    width: 80px;
  }
}
